body {
    color: #333332;
    background-color: #EBF1F4;
}
div.portfolio-intro .intro-desc, 
div.portfolio-summary .skills-list, 
div.portfolio-projects, .project {
    background-color: #ffffff;
    padding: 25px !important;
    border-radius: 15px;
}
/*header*/
.header-component {   
    position: fixed;
    top: 0;
    left: auto;
    width: 100%;
    padding: 5px 10px;
}

.header-component a {
    font-weight: 600 !important;
}
.header-component .portfolio-name a.name {
    color: #333332;
    text-decoration: none;
    cursor: pointer;
}
.portfolio-name h3 {
    padding: 10px 10px;
}
.scroll-change .portfolio-name h3, .scroll-change a {
    transition: color 1s ease;
    color: #ffffff !important;
}
.scroll-change {
    transition: background-color 1.5s ease;
    background-color:#1c286d;
    opacity: 0.9;
    top: 0;
    z-index: 10;
}
.scroll-none {
    transition: background-color 1s ease;
}
.scroll-none-div {
    transition: opacity 1s ease;
    opacity: 0.05;
}
.scroll-div {
    transition: opacity 1s ease;
    opacity: 1;
}

/*homepage*/
.portfolio-intro.ui.container.grid {
    margin-top: 75px;
    padding-top: 15px;
}
.portfolio-projects.ui.container.stackable.grid {
    margin: 34px 0;
}
.intro-desc {
    margin: 15px 0px 15px 0px;
    width: 75%;
}
.intro-desc .icon {
    font-size: 2.2rem !important;
    margin: 5px auto;
    padding-top: 15px;    
    -ms-transform: rotate(-20deg); /* IE 9 */
    -webkit-transform: rotate(-20deg); /* Safari 3-8 */
    transform: rotate(-20deg);
}
.profile-image { 
    display: inline-block;
    height: 100px;
    border-radius: 50%;
    margin: 10px 25px;
}
.portfolio-intro.ui.grid>.row>.column {
    padding-left: 0;
}
.portfolio-intro h2 {
    font-weight: 400!important;
    font-size: 1.2rem;
}
.portfolio-intro h1 {
    display: inline-block;
    width: 70%;
    vertical-align: top;
    font-family: 'Karla', sans-serif;
    font-weight: 700 !important;
    letter-spacing: 1px;
    font-size: 2.5rem !important;
    color: #1c286d!important;
}
.secondary-skill {
    padding-top: 40px;
}
.project-card {
    padding: 10px 0;
    margin: 10px;
    border-radius: 5px;
}

/* Project Page */
/**Projects Margins*****/
.ui.container.project-home.stackable.grid {
    margin-bottom: 25px;
}
.ui.projects-menu h4 {
    padding-left: 0 !important;
    font-weight: 400;
}
.ui.projects-menu a.row {
    padding: 5px 0 !important;
}
.ui.projects-menu.container.grid button {
    justify-content: center;
    width: 100%;
}

.three.wide.column.projects-sidenav {
    margin-right: 1rem;
    padding-left: 0;
}
.ui.project-home h1.project-home-title {
    margin: 100px 0 !important;
}

img.column.logo-image, 
.project-home .floated.column.button.logo-link {
    height: 100%;
    width: 200px !important;
}

.project-home .floated.column.button.logo-link, .ui.button.project-link {
    margin-top: 20px !important;
}

img.column.logo-image {
    margin-top: 5px!important;
}
.project-home .project-image {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 75%;
    height: auto;
    margin-top: 25px;
    margin-bottom: 25px;
}

.project-wrapper {
    display: flex;
    justify-content: center;
    margin: 0px 10px 25px 10px;
}

/*Site Styles*/
li {    
    padding: 3px 0;
}
li > span {
    vertical-align: top !important; 
    padding: 5px;
    font-size: 16px;
}
div.five.wide.column.skills-list ul,
.six.wide.column.skills-list ul {
    list-style: none;
    padding-left: 0;
} 
.five.wide.right.floated.column.skills-list ul {
    width: 60%;
    margin: auto;
}
.five.wide.left.floated.column.skills-list ul {
    width: 50%;
    margin: auto;
}

.five.wide.floated.column.skills-list h3 {
  text-align: center;
}

.projects div.eight.wide.column {
    padding-left: 0 !important;
}

hr.desktop-menu-divider {
    display: block;
    border-top: 1px solid #333332;
    margin: 45px auto !important;
    justify-content: center;
    text-align: center;
    width: 85%;

}
a.active button {
    background-color: #8c94b4 !important;
}
button.button.ui {
    color: #ffffff !important;
    background-color: #ed6f63;
    margin: 3.5px 3.5px 3.5px 0 !important;
    padding: 8px !important;
    font-size: 0.95rem;
}
button.button.ui:hover  {
    color: #ffffff!important;
    background-color: #8c94b4 !important ;
}

button.ui.button a {
    color: #ffffff;
}
.all-projects a, .portfolio-projects a, .projects-home a, a.contact-linkedin {
    font-weight: 600;
    cursor: pointer;
}

.all-projects a:hover,
.portfolio-projects a:hover,
.projects-home a:hover, 
a.contact-linkedin:hover {
    color: #ed6f63 !important;
}
i.icon {
    font-size: 1.4em !important;
    color: #ed6f63;
}
a.contact-linkedin, .contact-desc {
    color: #1c286d!important;
}

.choose-project {
    font-family: 'PT Sans', sans-serif;
    letter-spacing: 2px; 
    font-weight: 600;
}
.portfolio-main {
    padding-top: 45px;
    margin-bottom: 150px;
}

h1 {
    font-family: 'PT Sans', sans-serif;
    font-weight: 700;
    line-height: 1.24em;
}

h2, h3, h4 {
    font-family: 'PT Sans', sans-serif;
    letter-spacing: 1px; 
}

.project h4 {
    margin: 10px 0!important;
    margin-top: 25px !important;
}


/**footer**/
footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}

@media screen and ( max-width: 1024px ) { 
    .five.wide.column.project-card {
        max-width: 30% !important;
    }
    .intro-desc {
        width: 72% !important;
    }
 
    .five.wide.right.floated.column.skills-list ul,
    .five.wide.left.floated.column.skills-list ul {
        width: 70% !important;
    }
}
@media screen and ( max-width: 768px ) { 
    .portfolio-intro.ui.container.stackable.grid .row {
        text-align: center;
        justify-content: center;
    }
    .project-home .floated.column.button.logo-link {
        margin-left: 12px !important;
    }
    .five.wide.right.floated.column.skills-list ul,
    .five.wide.left.floated.column.skills-list ul {
        width: 95% !important;
    }

    .ui.column.grid>[class*="eleven wide"].column, 
    .ui.grid>.column.row>[class*="eleven wide"].column, 
    .ui.grid>.row>[class*="eleven wide"].column,
    .ui.grid>[class*="eleven wide"].column,
    .ui.column.grid>[class*="ten wide"].column, 
    .ui.grid>.column.row>[class*="ten wide"].column, 
    .ui.grid>.row>[class*="ten wide"].column, 
    .ui.grid>[class*="ten wide"].column {
        width: 60% !important;
    }
    
}

@media screen and ( max-width: 500px ) { 

    .ui.twelve.wide.column {
        display: flex;
    }
    .ten.wide.center.aligned.column.intro-desc {
        margin: 15px 0 !important;
    }
    .five.wide.left.floated.column.skills-list,
    .five.wide.right.floated.column.skills-list {
        margin: 15px 0px !important;
    }
    .five.wide.floated.column.skills-list ul {
        margin: 0 15px !important;
    }
    .ui.column.grid>[class*="eleven wide"].column, 
    .ui.grid>.column.row>[class*="eleven wide"].column, 
    .ui.grid>.row>[class*="eleven wide"].column,
    .ui.grid>[class*="eleven wide"].column,
    .ui.column.grid>[class*="ten wide"].column, 
    .ui.grid>.column.row>[class*="ten wide"].column, 
    .ui.grid>.row>[class*="ten wide"].column, 
    .ui.grid>[class*="ten wide"].column {
        width: 100% !important;
    }

    .header-component {
        padding-top: 1px !important;
        padding-bottom: 1px !important;
    }
    .ui.secondary.menu .item {
        padding: .5em .6em !important;
    }
    .header-component h3 {
        font-size: 1.2rem;
    }

    .five.wide.column.project-card {
        max-width: 80% !important;
    }
    /*projects home*/
    .all-projects {
        margin: 0px 20px;
    }
    div.six.wide.column.skills-list {
        padding: 5px !important;
    }
    .choose-project {
        justify-content: center;
        text-align: center;
    }
    img.floated.column.logo-image {
        margin-left: 0px !important;
    }
    .ui.project-home h1.project-home-title {
        margin-top: 50px !important;
        margin-bottom: 25px !important;
        font-size: 20px;
    }
    .ui.projects-menu h4 {
        margin-bottom: 8px !important;
    }
    .ui.projects-menu a.row {
        padding: 1px 0 !important;
    }
    h1.project-home-title {
        padding-left: 14px;
    }
    .menu-divider {
        display: block;
        border-top: 1px solid #333332;
        margin: 25px auto !important;
        justify-content: center;
        text-align: center;
        width: 75%;
    }
}

@media screen and ( max-width: 376px ) { 
    .portfolio-intro h1 {
       font-size: 1.6rem !important;
    }

    .five.wide.column.skills-list {
        max-width: 100% !important;
        margin: auto;
    }
}

